          <div class="box-bor">
            <h1></h1>
            <table class="table">
              <thead>
                <tr>
                  <th style="width:8%;"></th>
                  <th style="width:8%;"></th>
                  <th style="width:8%;"></th>
                  <th style="width:8%;"></th>
                  <th style="width:8%;"></th>
                  <th style="width:8%;"></th>
                  <th style="width:8%;"></th>
                  <th style="width:8%;"></th>
                  <!-- <th style="width:18%;"></th> -->
                </tr>
              </thead>
              <tbody>
                <tr class="active" v-for="item in items">
                  <td>{{item.id}}</td>
                  <td></td>
                  <td>{{item.agency_name}}</td>
                  <td>
                    <router-link to="/client">{{item.shagnhu_name}}</router-link>
                  </td>
                  <td>{{item.muban_name}}</td>
                  <td>{{item.muban_price}}</td>
                  <td>{{item.state}}</td>
                  <td>{{item.add_time}}</td>
                  <!-- <td>
                    <router-link to="/nas_order"></router-link>|
                    <router-link to="/nas_order"></router-link>|
                    <a to="javascript:void(0)"></a>|
                    <router-link to="/Recharge"></router-link>
                  </td> -->
                </tr>
              </tbody>
            </table>
          </div>
