<script src="https://cdn.bootcss.com/jquery/2.1.4/jquery.min.js"></script>
<script>
import homeTab from '@/components/Tab'
export default {
  name: 'shenghe',
  data() {
    return {
      items: [],
      aa:homeTab._username,
    }
  },
  //
  components: {
    homeTab
  },
  //
      created() {
           
        var _this = this
        this.$http.get('http://123.207.41.93/jtct/index.php/Agency/Programadd/shenhe_resaule?name='+_this.aa, {
                emulateJSON: true
            }).then(function(res) {
                // console.log("aaa" + JSON.stringify(res.body))
                _this.items = res.body;
            }, function(err) {
                //  alert("" + err.body)
            
        })
    }
}
</script>

<style>
/**/

.tab-cont .item {
  width: 100%;
  height: 100%;
  color: #000;
}

.item .box {
  width: 100%;
  min-height: 450px;
  padding-bottom: 20px;
  background-color: white;
}

.box-bor {
  width: 90%;
  height: auto;
  margin: 0px auto;
  border-radius: 10px;
  padding-top: 40px;
  color: #333333;
}

.btn-primary {
  margin-left: 48%;
  margin-top: 20px
}
</style>



