<template>
    <div class="tab" js-tab="1">
        <homeTab></homeTab>
        <div class="tabzhanghao">
            <div id="tab1" class="tab1">
                <a to="javascript:void(0)" v-on:click="showTab('1')"></a>
            </div>
            <div id="tab2" class="tab2">
                <a to="javascript:void(0)" v-on:click="showTab('2')"></a>
            </div>
        </div>
        <div class="tab-cont">
            <ul class="tab-cont__wrap">
                <!---->
                <li class="item" id="cont1" style="display:block;">
                    <div class="box">
                        <div class="box-bor">
                            <table class="table">
                                <caption style="text-align:center;font-size:26px;"></caption>
                                <thead>
                                    <tr>
                                        <th style="width:6%;"></th>
                                        <th style="width:10%;"></th>
                                        <th style="width:10%;"></th>
                                        <th style="width:10%;"></th>
                                        <th style="width:10%;"></th>
                                        <th style="width:10:%;"></th>
                                        <th style="width:10%;"></th>
                                        <th style="width:12%;"></th>
                                        <th style="width:6%;"></th>
                                        <th style="width:16%;"></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr class="active" v-for="item in xfitems">
                                        <td>{{item.running_num}}</td>
                                        <td>{{item.realname}}</td>
                                        <td></td>
                                        <td>{{item.appmcheng}}</td>
                                        <td>{{item.deal_type}}</td>
                                        <td>{{item.deal_gold}}</td>
                                        <td>{{item.balance}}</td>
                                        <td>{{item.time1}}</td>
                                        <td>{{item.state}}</td>
                                        <td>{{item.appmcheng}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </li>
                <!---->
                <li class="item" id="cont2" style="display:none;">
                    <div class="box">
                        <div class="box-bor">
                            <table class="table">
                                <caption style="text-align:center;font-size:26px;"></caption>
                                <thead>
                                    <tr>
                                        <th style="width:5%;"></th>
                                        <th style="width:10%;"></th>
                                        <th style="width:8%;"></th>
                                        <th style="width:8%;"></th>
                                        <th style="width:10%;"></th>
                                        <th style="width:6%;"></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr class="active" v-for="item in czitems">
                                        <td>{{item.state}}</td>
                                        <td>{{item.user_company}}</td>
                                        <td>{{item.recharge_gold}}</td>
                                        <td>{{item.recharge_time}}</td>
                                        <td>{{item.state}}</td>
                                        <td>{{item.content}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </li>
            </ul>
            <div class="bottom">
                <p>2017</p>
            </div>
        </div>
    </div>
</template>
<script>
import homeTab from '@/components/Tab'
import { formatDate } from '../../assets/js/date'
export default {
    name: 'nas_wallet',
    data() {
        return {
            xfitems:[],
            recharge_gold: '',
            recharge_time: '',
            user_company: '',
            content: '',
            company: '',
            state: '',
            czitems:[],
            running_num: '',
            deal_type: '',
            deal_gold: '',
            balance: '',
            dj_balance: '',
            deal_time: '',
            time: '',
            time1: '',
            state: '',
            appmcheng: '',
            realname: '',

        }
    },
    //
    components: {
        homeTab
    },
    created() {
        //
        this.$http.get('sz800800.cn/index.php/Agency/recharge/recharge_list?id=1', {
            emulateJSON: true
        }).then(function(res) {
            this.czitems = res.body
            console.log("" + JSON.stringify(res.body))
        }, function(err) {
            //  alert("" + err.body)
        }),

        //
            this.$http.get('sz800800.cn/index.php/Agency/deal/deal_list?id=1', {
                emulateJSON: true
            }).then(function(res) {
                this.xfitems = res.body
                console.log("" + JSON.stringify(res.body))
            }, function(err) {
                //  alert("" + err.body)
            })
    },
    methods: {
        showTab: function(id) {
            var a = function(id) {
                return document.getElementById(id);
            }
            for (var i = 1; i < 3; i++) {
                if (id == i) {
                    a("cont" + i).style.display = "block";
                } else {
                    a("cont" + i).style.display = "none";
                }
            }
            return false;

        },
        filters: {
            formatDate(time) {
                var data = data;
                return formatDate(data, 'yyyy-MM-dd hh:mm');
            },
            formatDate(time1) {
                var data1 = data1;
                return formatDate(data1, 'yyyy-MM-dd hh:mm');
            }
        }
    }
}
</script>
<style>
.tab_title {
    margin: 60px;
    padding: 20px
}
.box{
    margin: 40px;
}
</style>



